// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import 'babel-polyfill'
import App from './App'
import Axios from 'axios'
import router from './router'
// css重置
import "@/assets/css/base.css"
// rem 布局
import '@/assets/js/flexible.debug';
import '@/assets/js/flexible_css.debug';
// 引入swiper插件
import vueSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
// Alert 自定义提示框
import Alert from '@/components/public/Alert';
    
Axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
// 添加一个请求拦截器
Axios.interceptors.request.use(function(config) {
  // 配置统一请求路径
  process.env.NODE_ENV === 'production' ? config.url = "https://www.cicmorgan.com/svc/services/more/submitUserContactAddress" : config.url =  "https://www.cicmorgan.com/svc/services/more/submitUserContactAddress";
  return config;
}, function(error) {
  return Promise.reject(error);
});
Vue.use(vueSwiper);
Vue.prototype.$eventBus = new Vue();
Vue.prototype.$http = Axios;
Vue.config.productionTip = false

Vue.component('my-alert', Alert)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



