<template>
    <div class="hello">
        <div class="logo">
            <img src="../../assets/img/logo.png" alt />
        </div>
        <swiper :options="swiperOption" ref="mySwiper">
            <swiper-slide class="swiper-slide">
                <img src="../../assets/img/publicWelfare02.png" />
                <div class="word">
                    <p>全国每年产生的旧纺织品2600万吨</p>
                    <p>而再利用率还不到10%</p>
                    <p>想送给周围的亲戚朋友</p>
                    <p>未必拿得出手</p>
                    <p>想捐给红十字会这类大型公益组织</p>
                    <p>发现他们几乎不接受旧衣物捐赠</p>
                    <p>自己将衣服捐赠到贫困山区</p>
                    <p>费心费力运费奇高</p>
                    <p>还可能好心办坏事</p>
                </div>
            </swiper-slide>
            <swiper-slide class="swiper-slide">
                <img src="../../assets/img/publicWelfare02.png" />
                <div class="word">
                    <p>面对旧衣物回收</p>
                    <p>许多朋友是心存疑虑</p>
                    <p>最终选择的方式</p>
                    <p class="orange">丢弃</p>
                    <p>被丢弃的衣服逃不过埋填或焚烧</p>
                    <p>导致的结果是侵占大量土地资源</p>
                    <p>造成大量的资源浪费以及环境污染</p>
                </div>
            </swiper-slide>
            <swiper-slide class="swiper-slide">
                <img src="../../assets/img/publicWelfare02.png" />
                <div class="word">
                    <p>我们中投摩根一直心系公益事业</p>
                    <p>在公司五周年之际</p>
                    <p>特此发动公司的小伙伴和倡导我们的客户</p>
                    <p>一起做一些有意义的事情</p>
                    <p>把自己的旧衣物进行捐赠</p>
                    <p>捐赠者将衣物快递至中投摩根办公地址</p>
                    <p>由中投摩根汇集所有旧衣物后统一捐赠</p>
                    <p>给相关机构</p>
                    <p>同时所有参与爱心活动的用户都会享受到</p>
                    <p>中投摩根平台回馈的100元邮寄红包（抵</p>
                    <p>用卷）并收到平台邮寄的纪念小礼物</p>
                </div>
            </swiper-slide>
            <swiper-slide class="swiper-slide">
                <img src="../../assets/img/publicWelfare02.png" />
                <div class="word">
                    <p>每个人都多做一点点</p>
                    <p>我们的世界将变得更美好</p>
                    <p>更可持续发展</p>
                </div>
                <button class="donationBut" @click="showInfo"></button>
            </swiper-slide>
        </swiper>
        <div class="info" v-show="isShow">
            <img src="../../assets/img/publicWelfare05.png" />
            <div class="content">
                <div class="receInfo">
                    <h3>收货人信息：</h3>
                    <p>姓名：邓女士</p>
                    <p>手机：17701210353</p>
                    <p class="adress">地址：北京市海淀区莲花池东路39号西金大厦601</p>
                </div>
                <div class="donationInfo">
                    <h3>捐赠人信息：</h3>
                    <p>姓名：<input type="text" v-model="donationInfo.name"/></p>
                    <p>手机：<input type="text" v-model="donationInfo.mobilePhone"/></p>
                    <p class="zipCode">邮编：<input type="text" v-model="donationInfo.postcode"/></p>
                    <p>地址：<textarea v-model="donationInfo.street"></textarea></p>
                </div>
                <div class="tip">
                    注：<br/>
                    1、捐赠者所填写的邮寄地址需要与发货人地址（捐赠旧衣物）相符，以便平台回寄纪念小礼物；<br/>
                    2、提交信息后请与中投摩根客服联系领取抵用卷；<br/>
                    客服电话400-666-9086
                </div>
                <span class="sub" @click="sub">提交信息</span>
            </div>
        </div>
        <div class="indicate" v-show="isbtn">
        </div>
    </div>
</template>

<script>
import Qs from 'qs'
export default {
    name: "HelloWorld",
    data() {
        return {
            donationInfo: {
                name: '',
                mobilePhone: '',
                postcode: '',
                street: ''
            },
            isShow: false,
            isbtn: true,
            swiperOption: {
                notNextTick: true,
                direction: "vertical",
                effect: "fade",
                grabCursor: true,
                setWrapperSize: true,
                autoHeight: true,
                slidesPreView: 1,
                pagination: ".swiper-pagination",
                paginationClickable: true,
                height: window.innerHeight,
                mouseWheel: false,
                mousewheelControl: false,
                resistanceRatio: 0,
                observeParents: true,
                //设置点击箭头
                navigation: {
                    nextEl: ".swiper-button-next",
                    prevEl: ".swiper-button-prev"
                },
                //自动轮播
                // autoplay: {
                //     delay: 2000,
                //     //当用户滑动图片后继续自动轮播
                //     disableOnInteraction: false
                // },
                //开启循环模式
                // loop: true
                onTransitionStart(swiper) {
                    console.log(swiper);
                },
                on: {
                    //监听滑动切换事件，返回swiper对象
                    slideChange: () => {
                        let swiper = this.$refs.mySwiper.swiper;
                        console.log(swiper.activeIndex);
                        if(swiper.activeIndex==3){ //滑动打印当前索引
                            this.isbtn = false;
                        }
                    }
                }
            }
        };
    },
    methods: {
        // 点击参与捐赠显示信息手机
        showInfo() {
            console.log(this.isShow);
            this.isShow = true;
        },
        // 点击提交信息
        sub() {
            if(this.donationInfo.name == ""){
                this.$eventBus.$emit("alertShow", "请填写您的名字~");
                return false
            }
            if(this.donationInfo.mobilePhone == "" || !/^1[345678]\d{9}$/.test(this.donationInfo.mobilePhone)){
               this.$eventBus.$emit("alertShow", "请填写正确的手机号码哦~");
                return false
            }
            if(this.donationInfo.street == ""){
                this.$eventBus.$emit("alertShow", "请填写您的收获地址~");
                return false
            }
            this.$http.post('/myApi/svc/services/more/submitUserContactAddress', Qs.stringify(this.donationInfo))
            .then( data =>{
                this.$eventBus.$emit("alertShow", "信息采集成功");
            })
        }
    },
    computed: {
        swiper() {
            return this.$refs.mySwiper.swiper;
        }
    },
    mounted() {
        console.log(this);
        console.log("this is current swiper instance object", this.swiper);
        // this.swiper.slideTo(0, 1000, false); //初始滑动到哪个页面
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
    position: relative;
    height: 100%;
}
.swiper-slide {
    position: relative;
}
.swiper-slide img {
    width: 100%;
    height: 100%;
}
.swiper-slide .donationBut{
    width: 5rem;
    height: 1rem;
    border: 0;
    outline: 0;
    background: url('../../assets/img/donation.png') no-repeat;
    background-size: 100% 100%;
    position: absolute;
    bottom: 3.2rem;
    left: calc(100vw - 6.5rem);
}
.swiper-slide .word {
    width: 75%;
    height: auto;
    position: absolute;
    top: 4.85rem;
    left: 1.5rem;
}
.swiper-slide:nth-child(2) .word{
    top: 4.75rem;
}
.swiper-slide:nth-child(3) .word{
    top: 4.2rem;
    left: 1.35rem;
}
.swiper-slide:nth-child(4) .word{
    top: 5.2rem;
}
.swiper-slide p {
    font-size: 0.26rem;
    line-height: 0.48rem;
    letter-spacing: .08em;    
    font-family: Georgia
}
.swiper-slide .orange{
    color: orange;
    font-size: .38rem;
    line-height: .56rem;
}
.logo {
    width: 1.45rem;
    height: 0.65rem;
    position: absolute;
    top: 0.65rem;
    left: 0.45rem;
    z-index: 6;
}
.logo img {
    width: 100%;
    height: 100%;
}
.indicate{
    width: .75rem;
    height: .45rem;
    position: absolute;
    bottom: .45rem;
    left: 43.5%;
    z-index: 6;
    background: url("../../assets/img/btn.png") no-repeat;
    background-size: 100%;
}
.info{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 100%;
    z-index: 2;
}
.info img{
    width: 100%;
    height: 100%;
}
.info .content{
    width: 100%;
    box-sizing: border-box;
    position: absolute;
    top: 2rem;
    font-size: .28rem;
    line-height: .42rem;
}
.info .content .receInfo,.info .content .donationInfo{
    margin-bottom: .25rem;
    padding-left: 1.2rem;
}
.info .content .receInfo .adress{
    padding-right: .72rem;
    box-sizing: border-box;
}
.info .content .donationInfo p{
    line-height: .65rem;
}
.info .content .donationInfo .zipCode{
    margin-bottom: .11rem;
}
.info .content .donationInfo p:last-child textarea{
    vertical-align: top;
    line-height: top;
}
.info .content .donationInfo p input,.info .content .donationInfo p textarea{
    outline: none;
    border: 0;
    line-height: .52rem;
    background: #ececec;
    border-radius: .15rem;
    width: 4.2rem;
    padding-left: .15rem;
}
.info .content .tip{
    padding: 0 .45rem;
    margin-bottom: .35rem;
    line-height: .48rem;
}
.info .sub{
    width: 2.4rem;
    height: .6rem;
    background:green;
    border-radius: .3rem;
    display: block;
    margin: 0 auto;
    color: #fefefe;
    line-height: .6rem;
    text-align: center;
}
</style>



