<template>
  <div class="alert-wrapper" v-show="showStatus">
    <div class="alert-content" v-text="content"></div>
  </div>
</template>

<script>
export default {
  name: "alert",
  data() {
    return {
      // 显示状态
      showStatus: false,
      // 提示信息
      content: "",
      // 显示时间
      time: 1.2
    };
  },
  watch: {
    showStatus(val) {
      var self = this;
      if (val) {
        setTimeout(() => {
          self.showStatus = false;
        }, self.time * 1000);
      }
    }
  },
  created() {
    this.$eventBus.$on(
      "alertShow",
      function(content, time) {
        this.content = content;
        this.showStatus = true;
        this.time = time === null ? time : 2;
      }.bind(this)
    );
  }
};
</script>

<style scoped>
.alert-wrapper {
  display: table;
  width: auto;
  height: 0.6rem;
  line-height: 0.6rem;
  padding-left: 0.2rem;
  padding-right: 0.2rem;
  position: fixed;
  left: 50%;
  bottom: 30%;
  z-index: 999999999;
  transform: translate(-50%, -50%);
  background-color: rgba(58, 56, 56, 0.65);
  border-radius: 0.15rem;
}
.alert-content {
  display: table-cell;
  text-align: center;
  height: 100%;
  font-size: 0.28rem;
  color: #fff;
  font-weight: 100;
}
</style>



